import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import classNames from "classnames";
import React from "react";
import style from "../../assets/scss/index.module.scss";
import { IconLink, IconLinkProps } from "../IconLink";

type ProfileProps = {
    name: string
    job_title: string
    birthday: string
    email: string
    education: string
    about: string
    links: IconLinkProps[]
}

const ResumeProfile: React.FunctionComponent<ProfileProps> = (props: ProfileProps) => {
    const {
        name,
        links,
        job_title,
        birthday,
        email,
        education,
        about
    } = props;

    return <>
        <div className={ classNames(style.dMdFlex, style.alignItemsCenter, style.justifyContentSmBetween, style.gMb5) }>
            <h2 className={ classNames(style.gFontWeight300, style.gMr10) }>{ name }</h2>
            <ul className={ classNames(style.listInline, style.mb0) }>
                {
                    links.map((item: any) => {
                        return <IconLink key={ item.title } { ...item }/>
                    })
                }
            </ul>
        </div>
        <h6 className={ classNames(style.h6, style.gFontWeight300, style.gMb10) }>
            <i className={ classNames(style.gPosRel, style.gTop1, style.gMr5, style.gColorGrayDarkV5) }>
                <FontAwesomeIcon width={ 10.5 } height={ 14 }
                                 icon={ {
                                     prefix: "fas",
                                     iconName: 'award'
                                 } }/>
            </i>
            { job_title }
        </h6>
        <ul className={ classNames(style.listInline, style.gFontWeight300) }>
            {
                birthday && <li className={ classNames(style.listInlineItem, style.gMr20) }>
                    <i className={ classNames(style.gPosRel, style.gTop1, style.gMr8, style.gColorGrayDarkV5) }>
                        <FontAwesomeIcon width={ 10.5 } height={ 14 }
                                         icon={ {
                                             prefix: "fas",
                                             iconName: 'birthday-cake'
                                         } }/>
                    </i>
                    { birthday }
                </li>
            }
            {
                email && <li className={ classNames(style.listInlineItem, style.gMr20) }>
                    <i className={ classNames(style.gPosRel, style.gTop1, style.gMr8, style.gColorGrayDarkV5) }>
                        <FontAwesomeIcon width={ 10.5 } height={ 14 }
                                         icon={ {
                                             prefix: "fas",
                                             iconName: 'at'
                                         } }/>
                    </i>
                    { email }
                </li>
            }
            { education && <li className={ classNames(style.listInlineItem, style.gMr20) }>
                <i className={ classNames(style.gPosRel, style.gTop1, style.gMr8, style.gColorGrayDarkV5) }>
                    <FontAwesomeIcon width={ 10.5 } height={ 14 }
                                     icon={ {
                                         prefix: "fas",
                                         iconName: 'graduation-cap'
                                     } }/>
                </i>
                { education }
            </li> }
        </ul>
        <hr className={ classNames(style.gBrdGrayLightV4, style.gMy20) }/>
        <p className={ classNames(style.lead, style.gLineHeight18) }>
            { about }
        </p>
    </>
}

export default ResumeProfile;